import * as echarts from 'echarts';

export const drawline = (dom, result) => {
  let chartDom = dom
  let myChart = echarts.init(chartDom);
  let option;
  let xisdata = []
  let lonedata = []
  let isodata = []

  // duel with xAxis data
  result.forEach(item => {
    xisdata.push(item.A)
    lonedata.push(item.B)
    isodata.push(item.C)
  })

  // console.log(xisdata, lonedata, isodata)

  option = {
    title: {
      text: 'Loneliness and Social Isolation',
    },
    tooltip: {
      trigger: 'axis'
    },
    legend: {
      data: ['Loneliness', 'Social Isolation'],
      left: 'right'
    },
    // invisable toolbox
    toolbox: {
      show: false,
      feature: {
        dataZoom: {
          yAxisIndex: 'none'
        },
        dataView: { readOnly: false },
        magicType: { type: ['line', 'bar'] },
      }
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      data: xisdata
    },
    yAxis: {
      type: 'value',
      axisLabel: {
        formatter: '{value} Â°C'
      }
    },
    series: [
      {
        name: 'Loneliness',
        type: 'line',
        data: lonedata,
        markPoint: {
          data: [
            { type: 'max', name: 'Max' },
            { type: 'min', name: 'Min' }
          ]
        },
        markLine: {
          data: [
            { type: 'average', name: 'Average' }
          ]
        }
      },
      {
        name: 'Social Isolation',
        type: 'line',
        data: isodata,
        markPoint: {
          data: [
            { type: 'max', name: 'Max' },
            { type: 'min', name: 'Min' }
          ]
        },
        markLine: {
          data: [
            { type: 'average', name: 'Average' }
          ]
        }
      }
    ]
  };

  option && myChart.setOption(option);

}